<template>
    <v-col md="4" sm="12">
        <v-card outlined shaped :color="$vuetify.theme.dark ? '' : '#9ecd4399'" style="height:100%" v-for="(alert, i) in alerts" :key="i">
            <v-card-title>
                <v-card class="pa-2">Alertes</v-card>
                <v-spacer></v-spacer>
                <v-btn fab small elevation="1" @click="openDialog = 5; newsToUpdate = EmptyNews">
                    <v-icon>mdi-plus</v-icon>
                </v-btn>
            </v-card-title>
            <v-card-text>
                <v-card class="mx-auto" :color="$vuetify.theme.dark ? '#9ecd4399' : ''" elevation="4">
                    <v-card-title>Title</v-card-title>
                    <v-card-text>relating to or dependent on charity; charitable.
                        "an eleemosynary educational institution."
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <admin-menu menuType="alert" @open="openDialog = $event"></admin-menu>
                        <v-chip>Expire dans 1 jour</v-chip>
                    </v-card-actions>
                </v-card>
            </v-card-text>
        </v-card>
        <alert-modify :open="openDialog == 0 || openDialog == 5" :create="openDialog == 5" @close="openDialog = -1" @changed="changedAlert" :alert="alertToUpdate"></alert-modify>
        <admin-change :open="openDialog == 1" @close="openDialog = -1" @changed="deleteAlert" :title="'Supprimer cette alerte'" :msg="'Voulez vous vraiment supprimer cette alerte: '"
            validate="Supprimer" color="red darken-1">
        </admin-change>
    </v-col>
</template>

<script>
import adminMenu from './admin/adminMenu.vue'
import AlertModify from './admin/alertModify.vue';
import adminChange from './admin/adminChange.vue';
export default {
    components: {
        adminMenu, AlertModify, adminChange
    },
    name: "Alerts",
    methods: {
        changedAlert(value) {
            this.alertToUpdate = value
        },
        deleteAlert(value) {
            this.alertToUpdate = value
        }
    },
    data: () => ({
        openDialog: -1,
        alerts: [{
            title: "Title",
            subtitle: "relating to or dependent on charity; charitable.\"an eleemosynary educational institution.\"",
            text: "",
            photo: [],
            category: "",
            groups: [],
            link: "",
            expiration: 0
        }],
        alertToUpdate: {
            title: "",
            subtitle: "",
            text: "",
            photo: [],
            category: "",
            groups: [],
            link: "",
            expiration: 0
        }
    }),
}
</script>

